
import FetchCall from './Fetch';


const GetDataWeather = (type, lat, lon, item, callback) => {
  // apiKey
  let apikey = "4882bb2318db9a69e681e20cc403b2ac";

  // SessionStorage Coordinates
  sessionStorage.setItem("coord",JSON.stringify([lat,lon]));

  //
  if(type === "weather" || type === "forecast"){
    // -----
    if(type === "weather"){
      // -----
      const GetWeather = (lat,lon,apikey) => {
        FetchCall("//api.openweathermap.org/data/2.5/weather?lat=" + lat + "&lon=" + lon + "&appid=" + apikey + "&units=metric", function(data){
          let list = {};
          list.city    = data.name;
          list.temp    = data.main.temp + "°C";
          list.country = data.sys.country;
          list.weather = data.weather[0].description;

          if(1 >= data.wind.speed){
            list.wind = "No wind";
          }else{
            list.wind  = "Wind - " + data.wind.speed + " meter per second";
          }

          sessionStorage.setItem("list",JSON.stringify(list));

          return callback(list)
        })
      }
      if(sessionStorage.timestamp !== undefined){
        console.log("timestamp");
        let time    = sessionStorage.timestamp,
            newtime = new Date().toTimeString().split(" ")[0],
            minutes = 0,
            min_up  = 10;

        newtime = newtime.split(':')[0] * 60 + newtime.split(':')[1] * 1;
        minutes = newtime - time;
        console.log("limit - " + min_up);
        console.log("minutes - " + minutes);
        console.log("old minutes - " + time);
        console.log("new minutes - " + newtime);
        if(minutes >= min_up){
          console.log("update time");
          sessionStorage.setItem("timestamp", newtime);
          GetWeather(lat, lon, apikey);
        }else{
          console.log("no update time");
          let list = JSON.parse(sessionStorage.list);
          return callback(list);
        }
      }else{
        console.log("no timestamp");
        let time = new Date().toTimeString().split(" ")[0];
        GetWeather(lat, lon, apikey);
      }
      // -----
    }else{
      // -----
      // -----
    }
    // -----

  }
}

export default GetDataWeather;



